import React from 'react';
import Card from '../Card/Card';

function CardLights(props) {
  const lightsOn = props.lights.filter((light) => (light.value === 1));
  
  let text = 'Le statut des lumires est indtermin !';

  if (lightsOn.length === 0) {
    text = 'Toutes les lumires sont teintes.';
  }
  if (lightsOn.length === 1) {
    text = 'La lumire ' + lightsOn[0].singularComplement + ' ' + lightsOn[0].label + ' est allume.';
  }
  if (lightsOn.length > 1) {
    text = 'Les lumires ' + lightsOn.reduce((labels, item) => labels + item.label + ', ', '') + ' sont allumes.';
  }

  const overlay = props.loading ? 'Chargement...' : '';

  if (props.error) {
    return (
      <Card title={props.title} error overlay={overlay}>
        <p className="card-text">Oups une erreur est survenue dans la rcupration de l'tat des lumires !</p>
        <div className="text-right">
          <button className="btn btn-primary" onClick={props.onRetry}>R-essayer</button>
        </div>
      </Card>
    )
  }

  return (
    <Card title="Lumires" overlay={overlay}>
      <p className="card-text">{text}</p>
      <div className="text-right">
        <button className="btn btn-primary" onClick={props.onOffClick}>Tout teindre</button>
      </div>
    </Card>
  );
}

export default CardLights;
